import service from "../../service.js";
export default {
	data() {
		return {
			money:'',
			owner:'',
			bank:'',
			bankAcount:'',
			unfreeze:'',
			subBloon:true,
			moneyType:''
		}
	},
	methods: {
		jum(url){
			uni.navigateTo({
				url:url
			})
		},
		allsubmit(e){
			this.money = e;
		},
		submit(){
			/* if(this.subBloon){
				service.auth(this , service.api.record.withdrawSubmit , "POST" , {
				integral :this.money, 
				name :this.owner,
				bank :this.bank, 
				card :this.bankAcount 
			} , function(self , res){
				console.log(res);
				self.subBloon=false;
				// service.auth(self, service.api.record.index, 'GET', '', function(_self, res) {
				// 	console.log(res);
				// 	_self.unfreeze = res.data.unfreeze;
				// 	self.freeze = res.data.freeze;
				// 	self.paid = res.data.paid;
				// });
				self.unfreeze = res.data;
				uni.showToast({
					title:res.info,
					icon:'none'
				});
				setTimeout(()=>{
					self.subBloon=true;
					self.money='';
