					icon:'none'
				})
			} */
			
			
			
			if(this.subBloon){
			uni.request({
				url:service.api.record.withdrawSubmit,
				method:"POST",
				data:{
					integral :this.money,
					name :this.owner,
					bank :this.bank, 
					card :this.bankAcount 
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if(res.code == 1){
						this.subBloon=false;
					this.unfreeze = res.data;
					uni.showToast({
						title:res.info,
						icon:'none'
					});
					setTimeout(()=>{
						this.subBloon=true;
						this.money='';
					},3000);
					}else{
						uni.showToast({
							title:res.info,
							icon:'none'
						});
					}
					
				},
				fail: (res) => {
					uni.showToast({
						title:res.info,
						icon:'none'
					});
				}
			});
			}else{
				uni.showToast({
					title:'请3秒后再点击!',
					icon:'none'
				})
			}
			
		}
	},
	computed: {
		isOk() {
			if(this.owner.trim()!=='' && this.bank.trim()!=='' && this.bankAcount.trim() !==''){
				return true
			}else{
				return false
			}
			
		}
	},
	onLoad(op) {
		console.log(op)
		this.unfreeze = op.unfreeze;
	},
	onShow() {
		this.$service.auth(this , this.$service.api.record.withdrawDetail , "GET" , '' , function(self , res){
			console.log(res , "res");
			self.moneyType = res.data;
			self.owner = res.data.alipay_name;
			// self.bank = res.data.bank;
			self.bankAcount = res.data.alipay_account;
		});
	}
}
